import React, { useState, useEffect } from 'react';
import ReactMultiSelectCheckboxes from 'react-multiselect-checkboxes';
import ReactHtmlParser from 'react-html-parser';
import { getWallIDs } from '../../../../../../utils';
import { useSelector, shallowEqual } from 'react-redux';

const WallList = ({WallFilterDashbord }) => {
    const [Walls, setWalls] = useState([]);
    const [selectedWall, setSelectedWall] = useState([]);
    const { dashboarddata } = useSelector( (state) => state.dashboardDisplayReducer, shallowEqual );

    const updateWallList = () => {
        const wallArray = dashboarddata?.allWalllist?.map((wall) => ({ label: ReactHtmlParser(wall?.name), value: wall?.id, feeds:wall?.feedList?.length})) || [];
        setWalls(wallArray);
        setSelectedWall(wallArray);
        changeFeedsFilters(wallArray);
    };

    useEffect(() => {
        updateWallList();
    }, [dashboarddata?.allWalllist]);

    const changeFeedsFilters = (event) => {
        if (event.length > 0) {
            const WallIDs = getWallIDs(event);
            const isFeeds = event.some(value => value.feeds > 0); 
            setSelectedWall(event);
            WallFilterDashbord(WallIDs, isFeeds);
        }
    };

    return (<ReactMultiSelectCheckboxes options={Walls} onChange={changeFeedsFilters} value={selectedWall} classNamePrefix="check-feed" />)
};
 
 export default WallList