import React, { Suspense, useState } from "react";
import WallList from "../../Analytics/layout/common/WallList";
import TopFilter from "./common/topFilter";
import { Button, Card, CardBody, CardHeader, CardTitle, Table } from "react-bootstrap";

const LeaderBoard = React.lazy(() => import("../../Analytics/content/components/leaderBoard"));

const LeaderboardDashboard = (props) => {
  const { loader, leaderBoard, nevigationRedirect, isFeeds } = props;
  const [showMore, setShowMore] = useState(false)
  const isgetData = leaderBoard?.data?.length > 0;

  return (
    <div className={`mb-4 col-lg-${isgetData ? '7' : '7'}`}>
      <div className="home_leaderBoard h-100">
        <div className="p-0 card-custom  h-90 card h-100">
          <CardHeader className="px-3 pt-3 border-0 pb-0 w-100 d-flex align-items-center flex-wrap justify-content-between CustomLeaderboardHeader">
            <CardTitle>
              <h6 className="card-label font-weight-bolder">Leaderboard</h6>
              {leaderBoard?.data?.length > 4 ? <Button variant="link" className="p-0 ms-2 d-none" onClick={() => setShowMore(true)}> See More </Button> : ''}
            </CardTitle>

            <div className="leaderboard-filter d-flex align-items-center flex-wrap">
              <WallList {...props} />
              <div className="date-filter ms-2">
                <TopFilter {...props} />
              </div>
              {leaderBoard?.data?.length > 4 ? <Button variant="link" className="p-0 ms-2" onClick={() => setShowMore(true)}> See More </Button> : ''}
            </div>

          </CardHeader>
          {isgetData || loader ?
            <Suspense fallback={""}>
              <LeaderBoard isDashboard={1} loader={loader} selectedWallFeed={[]} leaderBoard={leaderBoard} {...props} showMore={showMore} hideShowMore={() => setShowMore(false)} />
            </Suspense>
            : isFeeds ? <div className="p-10 card-body">
              <div className="not_connected_err not_connected_err_new flex-center overlay-layer d-flex flex-column flex-center h-100">
                <p className="text-dark w-70 text-center font-size-h6">
                Oops! Couldn't find the content in the given fields. Try changing the date or selecting a different wall.
                </p>
              </div>
            </div> : <div className="p-10 card-body">
              <div className="not_connected_err not_connected_err_new flex-center overlay-layer d-flex flex-column flex-center h-100">
                <p className="text-dark w-70 text-center font-size-h6">
                  Create a feed and discover the most active audience creating content for your event through
                </p>
                <div className="not_conne_overlay d-flex flex-column flex-center">
                  <a onClick={() => nevigationRedirect()} className="btn btn-primary" href="#" > Add Feed </a>
                </div>
              </div>
            </div>}
        </div>
      </div>
    </div>
  );
};

export default React.memo(LeaderboardDashboard);
