import moment from "moment";
import React from "react";
import DateRangePicker from "react-bootstrap-daterangepicker";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";

const TopFilter = (props) => {
    const { startDate, endDate, filterDashbord } = props;

    return (
        <div className="bookCall-datepicker cursor-pointer">
            <DateRangePicker
                startDate={startDate}
                endDate={endDate}
                initialSettings={{
                    locale: { format: "DD-MM-YYYY", separator: "-" },
                    maxDate: moment().endOf("month").format("DD-MM-YYYY"),
                    showDropdowns: false,
                    startDate: startDate,
                    endDate: endDate,
                    ranges: {
                        Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                        Yesterday: [moment().subtract(1, "days").format("DD-MM-YYYY"), moment().subtract(1, "days").format("DD-MM-YYYY"),],
                        "Last 7 Days": [moment().subtract(6, "days").format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                        "Last 30 Days": [moment().subtract(30, "days").format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                        "Last Month": [moment().subtract(1, "month").startOf("month").format("DD-MM-YYYY"), moment().subtract(1, "month").endOf("month").format("DD-MM-YYYY"),],
                    },
                }}
                onCallback={filterDashbord}
            >
                <div className="d-flex align-items-center dashboard_dataRange dateRange">
                    <FontAwesomeIcon type={'regular'} icon={'calendar-week'} iconClass={''} />
                    <div className="d-flex align-items-center db_dateRange ms-2">
                        <div className="db_date"> {moment(startDate, "DD-MM-YYYY").format("ll")} </div>
                        <span>-</span>
                        <div className="db_date"> {moment(endDate, "DD-MM-YYYY").format("ll")}{" "} </div>
                        <FontAwesomeIcon type={'solid'} icon={'angle-down'} iconClass={'fs-6 ms-1'} />
                    </div>
                </div>
            </DateRangePicker>
        </div>
    );
};

export default React.memo(TopFilter);
